.context-wrapper {
  display: flex;
  justify-content: center;
  height: 100%;
}
.column-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.dropbox {
  padding: 10px;
  width: 250px;
  min-height: 500px;
}
.dragbox {
  user-select: none;
  padding: 15px;
  margin: 0 0 10px 0;
  min-height: 50px;
  color: #fff;
}
